&-Intro {
  color: $nvgum;
  display: flex;
  padding-bottom: 80px;
  &-Text {
    max-width: 50%;
    h2 {
      font-size: 5em;
      font-weight: 600;
      letter-spacing: -1.12px;
      line-height: 1.12;
      margin: 0 0 30px;
    }
    p {
      font-size: 1.8em;
      letter-spacing: 0;
      line-height: 1.6;
      &.author {
        font-weight: 600;
        margin: 0 0 25px;
        strong {
          font-size: 1.2em;
          display: block;
          color: $tango;
          font-weight: 600;
        }
      }
    }
  }
  &-Image {
    background-color: $sun;
    position: relative;
    flex-grow: 1;
    border-radius: 70px 0 0 70px;
    margin-left: 15px;
    margin-right: -85px;
    img {
      position: absolute;
      border-radius: 10px;
      left: 45%;
      margin-left: -200px;
      bottom: -80px;
    }
  }
}
