&-Featured {
  flex-grow: 1;
  position: relative;
  img {
    width: 100%;
  }
  &-Meta {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 50px;
    p {
      font-weight: 300;
      &.Meta {
        font-size: 2em;
        font-weight: 500;
        letter-spacing: -0.45px;
        line-height: 1.5;
        opacity: 0.8;
        span {
          margin: 0 10px;
        }
      }
    }
    h3 {
      font-size: 7em;
      font-weight: 600;
      letter-spacing: -1.56px;
      line-height: 1.1;
      margin: 0;
    }
  }
}
